<script>
import Vue from "vue";
import VModal from "vue-js-modal";
import DHeader from "../inc/dashboard_header";
import { PerfectScrollbar } from "vue2-perfect-scrollbar";
import VueCountdown from "@chenfengyuan/vue-countdown";
Vue.component(VueCountdown.name, VueCountdown);
Vue.use(VModal);

export default {
  name: "UserMain",
  data: function() {
    // var now = new Date();
    // var newYear = new Date(now.getFullYear() + 1, 0, 1);
    // console.log(newYear - now)
    return {
      progress: 35,
      counting: false,
      time: 1902011434, //newYear - now,
    };
  },
  methods: {
    startCountdown: function() {
      this.counting = true;
    },
    handleCountdownEnd: function() {
      this.counting = false;
    },
    showModal() {
      this.$modal.show("Notificate");
    },
    hideModal() {
      this.$modal.hide("Notificate");
    },
  },
  components: {
    "d-header": DHeader,
    PerfectScrollbar,
  },
  mounted: function() {
    this.showModal();
  },
};
</script>
